<template>
  <div class="archive">
    <transition
      name="router-anim"
      :enter-active-class="enterClass"
      :leave-active-class="leaveClass"
    >
      <Slide
        ref="slide"
        :data="slideArray[this.$route.params.id - 1]"
        :key="routeKey"
      />
    </transition>

    <div class="action-menu">
      <div @click="toggleAutoSlide" class="auto-slide">
        Auto slide: {{ autoSlide ? 'on' : 'off' }}
      </div>
      <div class="pagination">
        {{ $route.params.id }} / {{ slideArray.length }}
      </div>
    </div>

    <footer id="footer">
      <ul class="hashtag">
        <span v-for="(hashtag, index) in filter" :key="index" >
          <template v-if="keywords.includes(hashtag)">
            <li class="keyword__item keyword__item-selected">
              <router-link :to="{ name: 'Archive', params: { id: 1, hashtag: hashtag } }">{{ hashtag }}</router-link>
            </li>
          </template>
          <template v-else-if="hashtags.includes(hashtag)">
            <li class="hashtag__item hashtag__item-selected">
              <router-link :to="{ name: 'Archive', params: { id: 1, hashtag: hashtag } }">{{ hashtag }}</router-link>
            </li>
          </template>
          <template v-else>
            <li :class="hashtag.indexOf('#') === 0 ? 'hashtag__item' : 'keyword__item'">
              <router-link :to="{ name: 'Archive', params: { id: 1, hashtag: hashtag } }">{{ hashtag }}</router-link>
            </li>
          </template>
        </span>
      </ul>
    </footer>
  </div>
</template>

<script>
const lethargy = require('lethargy');
let scroll =  new lethargy.Lethargy();

import Slide from '@/components/Slide.vue';

import { mapState } from 'vuex';

export default {
  components: {
    Slide
  },
  props: ['filter'],
  data() {
    return {
      slideArray: [],
      keywords: [],
      hashtags: [],
      canNavigate: true,
      direction: null,
      enterClass: null,
      leaveClass: null,
      allowScroll: true,
      autoSlideTimer: null,
      autoSlide: true,
    }
  },
  watch: {
    'direction'() {
      if (this.direction === 'up') {
        this.enterClass = "animate__animated animate__slideInDown"
        this.leaveClass = "animate__animated animate__slideOutDown"
      } else if (this.direction === 'down') {
        this.enterClass = "animate__animated animate__slideInUp"
        this.leaveClass = "animate__animated animate__slideOutUp"
      }
    },
  },
  computed: {
    ...mapState([
      'data'
    ]),
    routeKey() {
      // generate unique keys for the slide component
      let key;
      if (this.$route.params.side !== undefined) {
        key = `${this.$route.params.id}_${this.$route.params.side}`
      } else {
        key = `${this.$route.params.id}`
      }
      return key
    }
  },
  beforeDestroy() {
    // clear out timer
    clearTimeout(this.autoSlideTimer)
  },
  destroyed() {
    // remove slide navigation event listeners
    window.removeEventListener('keydown', this.keyPress)
    window.removeEventListener('wheel', this.wheel)
  },
  mounted() {
    // add event listeners for slide navigation
    window.addEventListener('keydown', this.keyPress)
    window.addEventListener('wheel', this.wheel, { passive: false })

    // initiate automatic slide
    this.autoNavigate()

    // Slides
    for (let i = 0; i < this.data.length; i++) {
      if (this.data[i].trefwoorden.includes(this.$route.params.hashtag)) {
        // items keywords
        let keywordSplit = this.data[i].trefwoorden.split(', ')
        let hashtagSplit = this.data[i].hashtags.split(', ')
        for (let x = 0; x < keywordSplit.length; x++) {
          this.keywords.push(keywordSplit[x])
        }
        for (let y = 0; y < hashtagSplit.length; y++) {
          this.hashtags.push(hashtagSplit[y])
        }
        // slide array
        this.slideArray.push(this.data[i])
      }

      // hashtags
      if (this.data[i].hashtags.includes(this.$route.params.hashtag)) {
        // items hashtags
        let hashtagSplit = this.data[i].hashtags.split(', ')
        let keywordSplit = this.data[i].trefwoorden.split(', ')
        for (let a = 0; a < hashtagSplit.length; a++) {
          this.hashtags.push(hashtagSplit[a])
        }
        for (let z = 0; z < keywordSplit.length; z++) {
          this.keywords.push(keywordSplit[z])
        }
        // slide array
        this.slideArray.push(this.data[i])
      }
    }

    // item filters
    this.keywords = Array.from(new Set(this.keywords));
    // hashtag
    this.hashtags = Array.from(new Set(this.hashtags));
  },
  methods: {
    routeNumber(direction, current, array, offset) {
      current = parseInt(current)

      if (direction === 'up') {
        if ((current - 1) + offset === 0) {
          current = array.length
        } else {
          current = (current - 1) + offset;
        }
      } else if (direction === 'down') {
        if ((current + 1) + offset > array.length) {
          current = 1 + offset;
        } else {
          current = (current + 1) + offset;
        }
      }

      return current
    },
    toggleAutoSlide() {
      if (this.autoSlide) {
        this.autoSlide = false
        clearTimeout(this.autoSlideTimer)
      } else if (!this.autoSlide) {
        this.autoSlide = true;
        this.autoNavigate()
      }
    },
    autoNavigate() {
      let nr;
      if (this.autoSlide) {
        this.autoSlideTimer = setTimeout(() => {
          this.direction = 'down'
          nr = this.routeNumber(this.direction, this.$route.params.id, this.slideArray, 0)
          this.$router.push({ name: 'Archive', params: { id: nr, hashtag: this.$route.params.hashtag } })
          .catch((err) => {})

          this.autoNavigate()
        }, 10000)
      }
    },
    keyPress(e) {
      let nr;

      if(this.canNavigate) {
        // clear auto slides timeout
        clearTimeout(this.autoSlideTimer)
        switch (e.keyCode) {
          case 38:
            e.preventDefault()
            // change direction
            this.direction = 'up';
            // get new slide number
            nr = this.routeNumber(this.direction, this.$route.params.id, this.slideArray, 0)
            // push route to next slide
            this.$router.push({ name: 'Archive', params: { id: nr, hashtag: this.$route.params.hashtag } })
            .catch(err => {})

            // wait until slide animation (500ms) is done to prevent triggering
            // a double animation
            this.canNavigate = false;
            setTimeout(() => {
              this.canNavigate = true;
            }, 500)

            break;
          case 40:
            e.preventDefault()
            // clear auto slides timeout
            clearTimeout(this.autoSlideTimer)
            // change direction
            this.direction = 'down';
            // get new slide number
            nr = this.routeNumber(this.direction, this.$route.params.id, this.slideArray, 0)
            // push route to next slide
            this.$router.push({ name: 'Archive', params: { id: nr, hashtag: this.$route.params.hashtag } })
            .catch(err => {})

            // wait until slide animation is done (500ms) to prevent triggering
            // a double animation
            this.canNavigate = false;
            setTimeout(() => {
              this.canNavigate = true;
            }, 500)
            break;
        }
        // start auto slide timer
        this.autoNavigate()
      }
    },
    wheel(e) {
      // e.preventDefault()
      e.stopPropagation()

      let nr;
      let check = scroll.check(e)

      if (check !== false && this.allowScroll) {
        // clear auto slides timeout
        clearTimeout(this.autoSlideTimer)
        if (check == 1) {
          this.allowScroll = false;
          this.direction = 'up';

          nr = this.routeNumber(this.direction, this.$route.params.id, this.slideArray, 0)
          this.$router.push({ name: 'Archive', params: { id: nr, hashtag: this.$route.params.hashtag } })
          .catch(err => {})

          // allow scrolling after animation
          setTimeout(() => {
            this.allowScroll = true;
          }, 500)
        } else if (check == -1) {
          this.allowScroll = false;
          this.direction = 'down';

          nr = this.routeNumber(this.direction, this.$route.params.id, this.slideArray, 0)
          this.$router.push({ name: 'Archive', params: { id: nr, hashtag: this.$route.params.hashtag } })
          .catch(err => {})

          // allow scrolling after animation
          setTimeout(() => {
            this.allowScroll = true;
          }, 500)
        }
        // start auto slide timer
        this.autoNavigate()
      }
    },
  }
}

</script>

<style scoped>
  .archive {
    position: fixed;
    height: 100%;
    width: 100%;
  }

  .hashtags,
  .keywords {
    position: fixed;
    font-size: 1.25em;
    color: #ffffff;
    max-width: 350px;
    padding: 2.5em;
    bottom: 0;
  }

  .keywords {
      left: 0;
  }

  .hashtags {
    right: 0;
  }

  .hashtags ul li span::after,
  .keywords ul li span::after {
    content: ', '
  }

  .hashtags ul li:last-child span::after,
  .keywords ul li:last-child span::after {
    content: ''
  }

  .title {
    text-transform: uppercase;
  }

  .keyword__item-selected {
    border: 1px solid var(--geel);
  }

  .keyword__item {
    font-family: 'Redaction70Regular', serif;
  }

  .hashtag__item-selected {
    border: 1px solid var(--rood);
  }

  .hashtag__item {
    font-family: 'Redaction50Regular', serif;
  }

  .action-menu {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    cursor: default;
    position: fixed;
    font-family: 'Redaction50Regular';
    top: 3.25em;
    right: 2.5em;
  }

  .pagination, .auto-slide {
    color: #ffffff;
    padding: .25em .5em;
    background-color: var(--bruin);
    border: 1px solid #000000;
    border-radius: 1em;
  }

  .auto-slide {
    margin-bottom: .5em;
    cursor: pointer;
  }
</style>
