import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Archive from '../views/Archive.vue'

import store from '../store/index.js'

import qs from 'qs';

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    redirect: {
      name: 'Home',
      params: {
        id: 1
      },
    }
  },
  {
    path: '/:id',
    name: 'Home',
    component: Home,
    props: true,
  },
  {
    path: '/archive/:hashtag/:id',
    name: 'Archive',
    component: Archive,
    props: true,
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: '/moksi/',
  routes
})

export default router
