<template lang="html">
  <div class="slide">
    <transition name="fade">
      <div v-if="toggleTooltip" class="tooltip" :style="mouseMoveStyle">
        <template v-if="mouseTarget === 'left'">
          <span>{{ data.beschrijving }}, {{ data.datum }}</span><br/><br/>
          <span>{{ data.reportageserie }}</span><br/><br/>
          <span>{{ data.locatie }}</span><br/><br/>
          <span>{{ data.collectiearchief }}</span><br/>
          <span>{{ data.materiaalsoort }}</span><br/><br/>
          <span>bestanddeelnummer: {{ data.bestanddeelnummer }}</span><br/>
          <span>nummertoegang: {{ data.nummertoegang }}</span><br/><br/>
          <span>{{ data.auteursrechthebbende }}</span>
        </template>
        <template v-if="mouseTarget === 'right'">
          <span>{{ data.instagram_user }}</span><br/><br/>
          <span>{{ data.description }}</span><br/>
        </template>
      </div>
    </transition>
    <div class="blend" v-if="this.data !== undefined">
      <img ref="left" id="left" :src="require(`../assets/images/${this.data.images.left}`)">
      <img ref="right" id="right" :src="require(`../assets/images/${this.data.images.right}`)">
    </div>
  </div>
</template>

<script>


export default {
  props: ['data'],
  name: 'Slide',
  data() {
    return {
      test_image: null,
      mouseData: {
        y: 0,
        x: 0,
      },
      selectedMetaData: null,
      toggleTooltip: false,
      mouseTarget: null,
      imagePortrait: false,
      vw: null,
      vh: null,
    }
  },
  beforeDestroy() {
    this.toggleTooltip = false;
  },
  mounted() {
    window.addEventListener('mousemove', this.tooltip)
    window.addEventListener('resize', this.resize)
    this.resize()
  },
  computed: {
    mouseMoveStyle() {
      return { transform: `translate(calc(${this.mouseData.x}px + 1em), calc(${this.mouseData.y}px + 1em))` }
    },
  },
  methods: {
    resize() {
      this.vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0)
      this.vh = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0)

      if (this.vh > this.vw) {
        this.imagePortrait = true
      } else if (this.vw >= this.vh) {
        this.imagePortrait = false
      }

    },
    tooltip(e){
      let left = this.$refs.left,
      right = this.$refs.right,
      target = e.target.id;

      this.mouseData.y = e.clientY;
      this.mouseData.x = e.clientX;

      if (target === 'header') {
        this.mouseTarget = 'header'
      } else if (target === 'footer') {
        this.mouseTarget = 'footer'
      } else if (target === 'left') {
        this.mouseTarget = 'left'
      } else if (target === 'right') {
        this.mouseTarget = 'right'
      } else if (target === 'nav') {
        this.mouseTarget = 'nav'
      }

      if (this.mouseTarget === 'header' || this.mouseTarget === 'nav' || this.mouseTarget === 'footer') {
        this.toggleTooltip = false;
      } else if (this.mouseTarget === 'left' || this.mouseTarget === 'right') {
        this.toggleTooltip = true;
      }
    },
  },
  destroyed() {
    window.removeEventListener('mousemove', this.tooltip)
  }
}
</script>

<style>
.slide {
  color: white;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

img {
  width: 60%;
}

.blend {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: black;
}

.blend img:first-child {
  position: absolute;
  mask-image: linear-gradient(90deg, rgba(255,0,0,1) 80%, rgba(0,0,0,0) 100%);
}

.blend img:last-child {
  position: absolute;
  mix-blend-mode: difference;
  mask-image: linear-gradient(-90deg, rgba(255,0,0,1) 80%, rgba(0,0,0,0) 100%);
  right: 0;
  top: 0;
}

.tooltip {
  top: 0;
  left: 0;
  color: white;
  font-family: 'Redaction50Regular';
  max-width: 350px;
  position: fixed;
  font-size: .75em;
  z-index: 999;
  background: var(--bruin-opacity);
  border: 1px solid #000000;
  padding: .5em;
  border-radius: 1em;
}

</style>
