var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"expand-column"},[_vm._v(" MOKSI: Negotiated Memory is what connects the subjective with cultural identity of Surinamese women. What do keywords say about colonial history in archives? Many of these keywords and titles are made of inconclusive words, not even mentioning names of the people portrayed on the images. How much information is lost when keywords and hashtags are used? "),_c('br'),_c('br'),_vm._v(" The National Archive NL has a total of 10186 images using the keyword 'Suriname' on their website. Of this total there are 1605 pictures made during the visit of Queen Juliana and Prins Bernhard in 1955. And only 184 of these images use the keyword 'women'. As a Surinamese woman myself I find that there's a lack of Surinamese female role models in general. But this makes it even more clear how problematic this is. Surinamese women are culture carriers, so how come there's so little to find on the surface? "),_c('br'),_c('br'),_vm._v(" MOKSI intends to give Surinamese women their narratives back. By reversing the gaze to the viewer and remove power from the photographer, the power is given back to women portrayed on the images. How do they represent themselves on Instagram? Which hashtags do they use? By comparing keywords from National Archive NL to self-made hashtags I'm highlighting biases and representation of Surinamese women in institutional archives compared to DIY Instagram posts. By giving the viewer the opportunity to interact with the website, it becomes a narrative device as well as an alternative tool. "),_c('br'),_c('br'),_vm._v(" Yessica Deira (1994) is a graphic designer, DJ and producer experimenting in re-constructing collective consciousness through design, film, sound, music and performance. "),_c('br'),_c('br'),_vm._v(" Her work MOKSI: Negotiated Memory (2020) is an ongoing live performative piece in which the artist explores the private nature of analogue archives and the fast-paced outpouring of digital productions. She aims to expand her cultural knowledge about Surinam and tells her story through found footage questioning the preservation of archival material. "),_c('br'),_c('br'),_vm._v(" MOKSI intends to encourage the current and future continuers and consultants of the archive to speculate together about the form and interpretation. ")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"legend"},[_c('span',[_c('span',{staticClass:"dot yellow"}),_vm._v(" National Archive NL")]),_c('br'),_c('span',[_c('span',{staticClass:"dot red"}),_vm._v(" Instagram")]),_c('br'),_c('br')])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"expand-column"},[_vm._v(" The National Archive was founded in 1802 as a Dutch state fund archive. As a non-academic/researcher/scholar it can be quite challenging navigating archives. A lot of items are available for viewing in the building as many items still have to be digitized. All keywords from the National Archive NL have been translated by myself. Nationaalarchief.nl does have an English version of the website but they don't translate the titles nor the keywords. This shows again a problematic side towards the keywords. By translating them myself, it already gives another meaning to the images. "),_c('br'),_c('br'),_vm._v(" Instagram has been on the rise since 2012 and is available for everyone with access to internet. You are building a personal archive with every image you post on this platform. By the use of hashtags people are creating their own narratives. It's beautiful to see how Surinamese women embrace their culture through hashtags. ")])}]

