<template>
  <div id="moksi">
    <Header/>
    <transition mode="out-in" name="fade">
      <router-view :key="key" :filter="filter"/>
    </transition>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import { mapState } from 'vuex';

export default {
  name: 'Moksi',
  components: {
    Header,
  },
  data() {
    return {
      filter: [],
      aboutOne: false,
      aboutTwo: false,
      infoOne: false,
    }
  },
  mounted() {
    this.$nextTick(() => {
      console.log(this.$route);
    })

    let keywordArr = []
    let data = this.data

    for (let i = 0; i < data.length; i++) {
      let split = data[i].trefwoorden.split(', ')
      let hashSplit = data[i].hashtags.split(', ')
      for (let x = 0; x < split.length; x++) {
        keywordArr.push(split[x])
      }
      for (let y = 0; y < hashSplit.length; y++) {
        keywordArr.push(hashSplit[y])
      }
    }

    // items to lower case
    keywordArr = keywordArr.map(keyword => keyword.toLowerCase());
    // remove duplicates
    keywordArr = Array.from(new Set(keywordArr));
    // remove spaces
    keywordArr = keywordArr.map(s => s.trim());
    // set vue data
    this.filter = keywordArr;
  },
  computed: {
    ...mapState([
      'slides',
      'data',
    ]),
    key() {
      let key;
      if (this.$route.name === 'Home') {
       key = `${this.$route.name}`
     } else {
       key = `${this.$route.params.hashtag}`
     }
     return key;
    }
  },
}




</script>

